.contact-details {
  flex-direction: column;
  width: 75%;
  height: 50%;
  background: lightskyblue;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 100vh #00000069;
  padding: 15px;
  border-radius: 10px;
  .img-container{
    border-radius: 10px;
  }
}
